
// import logo from "@/assets/images/logo.png";
import { defineComponent, reactive, ref } from "vue";
import { useStore } from "vuex";
import { useRouter } from "vue-router";

/**
 * RESEARCH
 */
export default defineComponent({
  components: {},
  props: {
    imgSrc: {
      type: String,
      default: "",
    },
    title: {
      type: String,
      default: "",
    },
    paragraph: {
      type: Array,
      default: () => {
        return [""];
      },
    },
    paragraph2: {
      type: String,
      default: "",
    },
    linkSrc: {
      type: String,
      default: "",
    },
  },
  setup(props) {
    const store = useStore();
    const router = useRouter();
    const jumpToDetail = () => {
      router.push({
        path: "/research/" + props.linkSrc,
      });
    };
    return { store, jumpToDetail };
  },
});
