import {
  createRouter,
  createWebHistory,
  createWebHashHistory,
  RouteRecordRaw,
} from "vue-router";
import HomeView from "../views/HomeView.vue";
import PeopleView from "../views/people/people-view.vue";
import ResearchView from "../views/research/research-view.vue";
import PublicationAllView from "../views/publications/publication-all-view.vue";
import PublicationSelectedView from "../views/publications/publication-selected-view.vue";
import ResearchExample from "../views/research/detail/example-view.vue";
import Paper1 from "../views/publications/detail/Combination_Attacks_and_Defenses_on_SDN_Topology_Discovery.vue";
import PaperDetail from "@/views/publications/detail/detail-index.vue";

const routes: Array<RouteRecordRaw> = [
  {
    path: "/",
    name: "home",
    component: HomeView,
  },
  {
    path: "/people",
    name: "people",
    component: PeopleView,
    meta: { title: "people", isNavigationMenu: false },
  },
  {
    path: "/research",
    name: "research",
    component: ResearchView,
    meta: { title: "research", isNavigationMenu: false },
  },
  {
    path: "/research/example",
    name: "research-example",
    component: ResearchExample,
  },
  {
    path: "/publications/all",
    name: "publication-all",
    component: PublicationAllView,
    meta: { title: "all-publications", isNavigationMenu: false },
  },
  {
    path: "/publications/selected",
    name: "publication-selected",
    component: PublicationSelectedView,
    meta: { title: "selected-publications", isNavigationMenu: false },
  },
  {
    path: "/publications/detail",
    name: "detail",
    component: PaperDetail,
    meta: {
      title: "PaperDetail",
      isNavigationMenu: false,
    },
  },
];

const router = createRouter({
  // history: createWebHistory(process.env.BASE_URL),
  history: createWebHashHistory(),
  routes,
});

export default router;
