            "Torp Handler :performs five profiling operations. First, it identifies the type of every arrival TCP packet (i.e., request or response). Second, for each request, it records the timestamp tin when sending the request to the end-host. Third, when receiving a response, it records another timestamp tout. Fourth, it calculates the host side latency as t = tout  tin. Finally, it reports the latency data to the analyzer in the control plane.",
            "Trop handler Progress :1.Identifying packet type.2.Processing TCP requests and responses.3.Reporting latency data.4.Workflow.",
