            "B. Top-down synchronization of state writes: 1) The application issues write operations to the write engine that contain a set of state updates, which are encapsulated in multiple control plane packets to determine the validity of the packet transmission based on the reception and return of the ACK. 2) To ensure that control plane-driven state writing is not interrupted by data plane packets, it provides a hardware-compatible atomic mechanism to lock the entire data plane state and recycle new state updates during state writing.",
